/***********************************************
    NOTE CARDS
***********************************************/

.note {
    position: relative;
}

.posts-container article.note {
background: repeating-linear-gradient(0deg, rgba(80, 160, 210, 0.2) 0px, rgba(80, 160, 210, 0.2) 1px, transparent 1px, transparent 20px),repeating-linear-gradient(90deg, rgba(80, 160, 210, 0.1) 0px, rgba(80, 160, 210, 0.1) 1px, transparent 1px, transparent 20px), white;
}

body.dark-mode .note .post-content {
    background: #2b2e32;
}

.note h2 {
    display: none;
}



/* Add these to your assets/css/index.css file */

/* Date Archive Specific Styles */
.date-archive-header {
    margin: 0 auto 3rem;
    text-align: center;
    border-bottom: 3px solid #000;
}

.date-archive-title {
    color: var(--gray-dark);
    font-family: var(--display);
    font-size: clamp(32px, 5vw, 48px);
    font-weight: 800;
    letter-spacing: 0.02rem;
    line-height: 1.1;
    margin-bottom: var(--space-sm);
    text-wrap: pretty;
}

.date-archive-description {
    color: var(--gray-dark);
    font-size: var(--text-2xl);
    font-weight: 300;
    line-height: 1.4;
    margin: 0;
    padding-bottom: var(--space-2xl);
    text-wrap: balance;
}
